label {
  display: block;
  margin-top: 20px;
}

.form-control {
  margin-bottom: 20px;
}

.btn {
  margin-top: 5px;
  margin-bottom: 0px;
  background-color: #0f8bff;
  width: auto;
  height: auto;
  /* display: block; */
  display: inline-block;
  font-size: 16px;
  border: none;
  padding: 10px 20px;

  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn:focus {
  border-color: #27fa32;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.075) inset, 0px 0px 8px #80ffaa79;
}

.btn:active {
  transform: scale(0.9);
}

.MuiButton-contained {
  width: auto;
  height: auto;
}

.MuiButton-contained:active {
  transform: scale(0.98);
}

.mat-table {
  margin-top: 20px;
  margin-bottom: 10px;
  background-color: #eeeeee;
}

.MuiTableRow-root:hover {
  background: #eeeeee !important;
}

.profile-page {
  margin-top: 20px;
  margin-bottom: 100px;
  width: auto;
  height: auto;
  display: block;
}

