.form-control:focus {
  border-color: #0f8bff;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.075) inset, 0px 0px 10px #54acff;
}

.form-select {
  margin-top: 20px;
}

.form-select:focus {
  border-color: #0f8bff;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.075) inset, 0px 0px 10px #54acff;
}
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}
.navbar {
  height: 40px;
  position: sticky;
  background-color: #3f51b5;
}
.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:hover {
  color: #ffffff;
  background-color: #233441;
}

.navbar-nav {
  margin-left: auto;
  margin-right: inherit;
  color: #ffffff;
}

.nav-item {
  padding: 20px 10px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #ecebeb;
  /* padding: 20px 25px 30px; */
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  /* box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3); */
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.swal2-container.swal2-backdrop-show,
.swal2-container.swal2-noanimation {
  background: rgba(0, 0, 0, 0.8);
}

.container:focus {
  background: #233441;
  border: 1px solid #2d597a;
}

.alert-danger {
  color: #cf3844;
  background-color: #fab5ba;
  border-color: #f5c2c7;
}

.alert {
  position: relative;
  padding: 0.2rem 0.7rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}
